@import '../../index.scss';

.container {
    max-width: 1440px;
    margin: 0 auto;
}

.ticker {
    width: 100%;
    height: 70px;
    white-space: nowrap;
    display: flex;
    align-items: center;
    border-width: 0.5px 0px;
    border-style: solid;
    border-color: $color-black;
}

.text {
    font-family: $font-primary;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 70px;
    text-transform: uppercase;
    letter-spacing: 0em;
    color: #000000;
    margin-right: 20px;
}